    <div class="page input js_show">
      <div class="page__bd">
        <div class="weui-cells__title">Vue注册桥接并授权</div>
        <div class="weui-cells weui-cells_form">
          <div class="weui-cell">
            <div class="weui-cell__hd">
              <label class="weui-label">token</label>
            </div>
            <div class="weui-cell__bd">
              <input id="token" class="weui-input" type="text" :value="token">
            </div>
          </div>
          <div class="weui-cell">
            <div class="weui-cell__hd">
              <label class="weui-label">vtDeviceId</label>
            </div>
            <div class="weui-cell__bd">
              <input id="vtDeviceId" class="weui-input" type="text" :value="vtDeviceId">
            </div>
          </div>
        </div>
        <div class="weui-footer">
          <p class="weui-footer__text">授信更新时间 {{ queryTime }}</p>
        </div>
        <div class="weui-btn-area">
          <a id="btn1" class="weui-btn weui-btn_primary" href="javascript:" @click="oauth">oauth授信</a>
        </div>
      </div>

      <div class="page__bd">
        <div class="weui-cells__title">登录</div>
        <div class="weui-btn-area">
          <a
            id="btn2"
            class="weui-btn weui-btn_primary"
            href="javascript:"
            @click="login"
          >唤起登录</a>
        </div>
        <div class="weui-cells weui-cells_checkbox">
          <label class="weui-cell weui-check__label" for="s11">
            <div class="weui-cell__hd">
              <input
                type="checkbox"
                class="weui-check"
                name="checkbox1"
                id="s11"
                :checked="checkedFlag"
                @change="selected($event, '3')"
              >
              <i class="weui-icon-checked"></i>
            </div>
            <div class="weui-cell__bd">
              <p>普通资金账号</p>
            </div>
          </label>
          <label class="weui-cell weui-check__label" for="s12">
            <div class="weui-cell__hd">
              <input
                type="checkbox"
                name="checkbox1"
                class="weui-check"
                id="s12"
                @change="selected($event, '6')"
              >
              <i class="weui-icon-checked"></i>
            </div>
            <div class="weui-cell__bd">
              <p>两融账号</p>
            </div>
          </label>
          <label class="weui-cell weui-check__label" for="s13">
            <div class="weui-cell__hd">
              <input
                type="checkbox"
                name="checkbox1"
                class="weui-check"
                id="s13"
                @change="selected($event, '18')"
              >
              <i class="weui-icon-checked"></i>
            </div>
            <div class="weui-cell__bd">
              <p>期权账号</p>
            </div>
          </label>
        </div>
        <div class="weui-btn-area">
          <a
            id="btn14"
            class="weui-btn weui-btn_primary"
            href="javascript:"
            @click="loginEnhance"
          >唤起加强版登录</a>
        </div>
      </div>

      <div class="page__bd">
        <div class="weui-cells__title">分享</div>
        <div class="weui-btn-area">
          <a
            id="btn3"
            class="weui-btn weui-btn_primary"
            href="javascript:"
            @click="share('2')"
          >微信朋友圈分享</a>
        </div>
        <div class="weui-btn-area">
          <a
            id="btn4"
            class="weui-btn weui-btn_primary"
            href="javascript:"
            @click="share('1')"
          >微信好友分享</a>
        </div>
        <div class="weui-btn-area">
          <a
            id="btn5"
            class="weui-btn weui-btn_primary"
            href="javascript:"
            @click="share('4')"
          >QQ好友分享</a>
        </div>
        <div class="weui-btn-area">
          <a id="btn6" class="weui-btn weui-btn_primary" href="javascript:" @click="share('3')">微博分享</a>
        </div>
      </div>

      <div class="page__bd">
        <div class="weui-cells__title">导航</div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">H5 URL</label>
          </div>
          <div class="weui-cell__bd">
            <input
              id="url"
              class="weui-input"
              type="text"
              placeholder="请输入H5 URL"
              :value="url"
              @change="url=$event.target.value"
            >
          </div>
          <div id="btnClearText" class="weui-cell__ft" @click="url=''">
            <i class="weui-icon-clear"></i>
          </div>
        </div>
        <div class="weui-btn-area">
          <a id="btn7" class="weui-btn weui-btn_primary" href="javascript:" @click="navigateTo">前进</a>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label">原生 URL</label>
          </div>
          <div class="weui-cell__bd">
            <input
              id="url2"
              class="weui-input"
              type="text"
              placeholder="请输入原生 URL"
              :value="url2"
              @change="url2=$event.target.value"
            >
          </div>
          <div id="btnClearText2" class="weui-cell__ft" @click="url2=''">
            <i class="weui-icon-clear"></i>
          </div>
        </div>
        <div class="weui-btn-area">
          <a
            id="btn11"
            class="weui-btn weui-btn_primary"
            href="javascript:"
            @click="navigateNativeTo"
          >前进</a>
        </div>
        <div class="weui-btn-area">
          <a id="btn8" class="weui-btn weui-btn_primary" href="javascript:" @click="navigateBack">后退</a>
        </div>
      </div>

      <div class="page__bd">
        <div class="weui-cells__title">第三方调用</div>
        <div class="weui-btn-area">
          <a id="btn9" class="weui-btn weui-btn_primary" href="javascript:" @click="reqOK">模拟成功调用</a>
        </div>
        <div class="weui-btn-area">
          <a
            id="btn10"
            class="weui-btn weui-btn_primary"
            href="javascript:"
            @click="reqError"
          >模拟失败调用</a>
        </div>
      </div>

      <div class="page__bd">
        <div class="weui-cells__title">设置导航头</div>
        <div class="weui-footer">
          <p class="weui-footer__text">{{ clickedText }}</p>
        </div>
        <div class="weui-btn-area">
          <a id="btn12" class="weui-btn weui-btn_primary" href="javascript:" @click="navigateBar">设置</a>
        </div>
      </div>

      <div class="page__bd">
        <div class="weui-cells__title">微信支付</div>
        <div class="weui-btn-area">
          <a id="btn13" class="weui-btn weui-btn_primary" href="javascript:" @click="payTo">测试支付</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";

const log = str => {
  var d = new Date();
  console.log(d.toLocaleTimeString() + " " + d.getMilliseconds() + ":" + str);
};

export default {
  name: "app",
  mounted() {
    log("vue mounted");

    /**
     * 初始化桥接程序
     */
    /* global fastman */
    fastman.ready(() => {
      const _this = this;

      // 所有的桥接接口都必须在ready接口执行成功后才可被调用，如果需要在页面加载时就调用相关桥接接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
      fastman.oauth({
        success: function(res) {
          _this.token = res.token;
          _this.vtDeviceId = res.vtDeviceId;
          _this.queryTime =
            new Date().toLocaleTimeString() +
            " " +
            new Date().getMilliseconds();
        },
        fail: function(err) {
          log("oauth failure:" + JSON.stringify(err));
          alert("oauth failure:" + JSON.stringify(err));
        }
      });
    });
  },
  data() {
    return {
      token: "", // 授信凭证
      vtDeviceId: "", // 虚拟设备编号
      queryTime: "", // 授信查询时间
      fundList: ["3"], // 账户类型列表
      url: "https://m.baidu.com/",
      url2: "dfyj://dl/openaccount",
      clickedText: ""
    };
  },
  computed: {
    checkedFlag: function() {
      return this.fundList.indexOf("3") !== -1;
    }
  },
  methods: {
    /**
     * 执行oauth指令
     * 获取设备编号及会话token
     * 设备编号：每个移动设备都会产生对应的标识符，用于区分不同的移动设备
     * 会话token：则是维持服务端服务之间的会话状态，以上2个参数在oauth指令成功回调可获取
     */
    oauth() {
      const _this = this;
      fastman.oauth({
        success: function(res) {
          _this.token = res.token;
          _this.vtDeviceId = res.vtDeviceId;
          _this.queryTime =
            new Date().toLocaleTimeString() +
            " " +
            new Date().getMilliseconds();
        },
        fail: function(err) {
          log("oauth failure:" + JSON.stringify(err));
          alert("oauth failure:" + JSON.stringify(err));
        }
      });
    },

    /**
     * 唤起登录
     */
    login() {
      fastman.onLogin();
    },

    /**
     * [加强版登录]
     */
    loginEnhance() {
      var payload = {
        type: "1"
      };
      if (this.fundList.length > 0) {
        payload.fundtype = this.fundList.join(",");
      }
      payload.success = function(res) {
        if (res.isLogin == true) {
          if (res.bizSysId == 3) log("普通资金账号");
          else if (res.bizSysId == 6) log("两融账号");
          else if (res.bizSysId == 18) log("期权账户");
        }
      };
      fastman.onLoginEnhance(payload);
    },

    /**
     * 分享
     */
    share(channel) {
      fastman.onShare({
        channel: channel,
        title: "东方赢家财富版上线啦",
        description: "东方赢家财富版是一款综合类金融工具APP",
        thumbImageUrl:
          "http://wxs.dfzq.com.cn/fileRes/new_download/images/new_pic.jpg",
        link:
          "http://wxs.dfzq.com.cn/fileRes/new_download/app_open/appOpen.html"
      });
    },

    /**
     * 导航前进(H5)
     */
    navigateTo() {
      var url = this.url;
      if (url == null || url == "") {
        log("请填写正确的h5 url");
      } else {
        if (window.localStorage) {
          var storage = window.localStorage;
          storage["url"] = url;
        }

        fastman.onNavigateTo({
          title: "跳转地址标题",
          uri: url
        });
      }
    },

    navigateBack() {
      fastman.onNavigateBack({
        success: function(resp) {
          log("返回成功");
        }
      });
    },

    navigateNativeTo() {
      var url = this.url2;
      if (url == null || url == "") {
        log("请填写正确的schema url");
      } else {
        if (window.localStorage) {
          var storage = window.localStorage;
          storage["url2"] = url;
        }

        fastman.onNavigateNativeTo({
          url: url
        });
      }
    },

    /**
     * 第三方调用 - 模拟成功回调
     */
    reqOK() {
      fastman.onReq("IF060002", {
        days: "2",
        success: function(resp) {
          alert(JSON.stringify(resp));
        },
        fail: function(err) {
          alert(JSON.stringify(err.info + "(" + err.code + ")"));
        }
      });
    },

    /**
     * 第三方调用 - 模拟失败回调
     */
    reqError() {
      fastman.onReq("IF060002", {
        days: fastman.isFromAndroid() ? "aaa" : 2, // 故意传错数据类型
        success: function(resp) {
          alert(JSON.stringify(resp));
        },
        fail: function(err) {
          alert(JSON.stringify(err.info + "(" + err.code + ")"));
        }
      });
    },

    /**
     * 设置导航头
     */
    navigateBar() {
      const _this = this;
      fastman.onNavigateBar({
        title: "我的标题", // 导航标题
        rightButtonTitle: "点我", // 右侧按钮文字
        onRightButtonPress: function() {
          // 右侧按钮点击事件，不定义则不会触发点击
          log("右侧按钮被点击了");
          _this.clickedText = "点了 ^_^";
        },
        isShowBackButton: true // 是否显示后退按钮，默认true(即显示)
      });
    },

    payTo() {
      fastman.onNavigateTo({
        title: "微信支付",
        uri: "../../wx-pay.html"
      });
    },

    /*
     * 私有逻辑
     */
    selected(e, type) {
      var _f = this.fundList;
      var _i = _f.indexOf(type);
      if (_i === -1) {
        _f.push(type);
      } else {
        _f.splice(_i, 1);
      }
      this.fundList = _f;
    }
  }
};
</script>

